import React, { useState } from 'react'
import { useNavigate } from "react-router-dom";
import { Tabs, Menu, Carousel, Collapse } from 'antd';
import { Route, Switch, HashRouter, Link } from 'react-router-dom';
import Footers from './../components/footer';
import {
  HomeOutlined,
  OrderedListOutlined,
  UserOutlined,
  VideoCameraOutlined,
  UploadOutlined,
  SnippetsOutlined
} from '@ant-design/icons';
const { Panel } = Collapse;
function Home (props) {
  console.log(props)
  let lan = props.language;
  const onChange = (key) => {
    console.log(key);
  };
  const text = lan === 'en' ? `
    A dog is a type of domesticated animal.
    Known for its loyalty and faithfulness,
    it can be found as a welcome guest in many households across the world.
  `: `
  狗是一种家养的动物。
它以忠诚和忠诚而闻名，
它在世界各地的许多家庭中都是受欢迎的客人。
  `;
  const tittxt = (index) => {
    return `${lan === 'en' ? `This is panel header` : `这是面板标题`} ${index}`
  }
  return (
    <div id="content">
      <header>
        <div className='btn-group'>
          <Link className='btn active' to='/home'><HomeOutlined />{lan === 'cn' ? '首页' : 'home'}</Link>
          <Link className='btn ' to='/list'><OrderedListOutlined />{lan === 'cn' ? '产品列表' : 'list'}</Link>
          <Link className='btn' to='/detail'><SnippetsOutlined />{lan === 'cn' ? '产品详情' : 'detail'}</Link>
        </div>
      </header>

      <Carousel>
        <div>
          <img src="/images/1.jpg" alt="" />
        </div>
        <div>
          <img src="/images/2.jpg" alt="" />
        </div>
        <div>
          <img src="/images/3.jpg" alt="" />
        </div>
        <div>
          <img src="/images/4.jpg" alt="" />
        </div>
      </Carousel>
      <Collapse defaultActiveKey={['1']} onChange={onChange}>
        <Panel header={tittxt(1)} key="1">
          <p>{text}</p>
        </Panel>
        <Panel header={tittxt(2)} key="2">
          <p>{text}</p>
        </Panel>
        <Panel header={tittxt(3)} key="3">
          <p>{text}</p>
        </Panel>
      </Collapse>
    </div>

  )
}

export default Home