
import { Route, useParams, Switch, HashRouter, Link } from 'react-router-dom';
import React, { useState } from "react";
function Footers (props) {
  let params = useParams();//获得地址栏参数
  console.log(params)
  // let data = JSON.parse(params.data);
  // 子向父传值
  const { setMaking } = props;
  const [language, setlanguage] = useState("cn");
  return (
    <div id="footers" className="btn-group">
      <div className='btn' onClick={() => {
        setlanguage('en')
        setMaking('en')
      }}>
        {language === 'cn' ? '英文' : 'English'}
      </div>
      <div className='btn' onClick={() => {
        setlanguage('cn')
        setMaking('cn')
      }}>
        {language === 'cn' ? '中文' : 'Chinese'}
      </div>
    </div>

  )
}

export default Footers