import logo from './logo.svg';
import './App.css';
import React, { useState } from "react";
import {
  BrowserRouter as Router,
  useRoutes,
} from "react-router-dom";
import Home from './home/index';
import List from './list/index';
import Detail from './detail/index';
import Footer from './components/footer';

const App = (props) => {
  console.log(props);
  props = props.language
  let routes = useRoutes([
    { path: "/", element: <Home language={props} /> },
    { path: "/home", element: <Home language={props} /> },/* 首页 */
    { path: "/list", element: <List language={props} /> },/* 产品列表页面 */
    { path: "/detail", element: <Detail language={props} /> },/* 产品详情页 */
  ]);
  return routes;
};

const AppWrapper = () => {
  /* 改变中英文 */
  const [language, setlanguage] = useState("cn");
  const setMaking = (val) => {
    console.log(val)
    setlanguage(val)
  }

  return (
    <Router>
      
      <App language={language} />
      <Footer setMaking={setMaking} />
    </Router>
  );
};

export default AppWrapper;
