import React, { useState } from 'react'
import { useNavigate } from "react-router-dom";
import { Tabs, Menu, Card, Row, Col, Button } from 'antd';
import { Route, Switch, HashRouter, Link } from 'react-router-dom';
import {
  HomeOutlined,
  OrderedListOutlined,
  UserOutlined,
  VideoCameraOutlined,
  UploadOutlined,
  SnippetsOutlined
} from '@ant-design/icons';
const { Meta } = Card;
function List (props) {
  console.log(props)
  let lan = props.language;
  const [list, setlist] = useState([
    {
      'png': 'https://picsum.photos/id/237/200/200',
    }, {
      'png': 'https://picsum.photos/id/238/200/200',
    }, {
      'png': 'https://picsum.photos/id/239/200/200',
    }, {
      'png': 'https://picsum.photos/id/240/200/200',
    }, {
      'png': 'https://picsum.photos/id/241/200/200',
    }, {
      'png': 'https://picsum.photos/id/254/200/200',
    }, {
      'png': 'https://picsum.photos/id/255/200/200',
    }, {
      'png': 'https://picsum.photos/id/256/200/200',
    }, {
      'png': 'https://picsum.photos/id/257/200/200',
    }, {
      'png': 'https://picsum.photos/id/258/200/200',
    },
  ])
  const navigate = useNavigate();


  return (
    <div id="content">
      <header>
        <div className='btn-group'>
          <Link className='btn' to='/home'><HomeOutlined />{lan === 'cn' ? '首页' : 'home'}</Link>
          <Link className='btn active' to='/list'><OrderedListOutlined />{lan === 'cn' ? '产品列表' : 'list'}</Link>
          <Link className='btn' to='/detail'><SnippetsOutlined />{lan === 'cn' ? '产品详情' : 'detail'}</Link>
        </div>
      </header>
      <Row gutter={16}>
        {
          list.map((ele, i) => {
            return (
              <Col span={12} key={i}>
                <Card
                  hoverable
                  cover={<img alt="example" src={ele.png} />}
                >
                  <Meta title={lan === 'cn' ? '描述信息' : 'Europe Street beat'} description="www.instagram.com" />
                  <Link to='/detail'>{lan === 'cn' ? '显示详情' : 'detail'}</Link>
                </Card>
              </Col>
            )
          })
        }
      </Row>

    </div>

  )
}

export default List